<script>
	//MO = MOUSE OWNER

	export let color = "red" //playercolor

	let checked = true //owner
	let tickrate = 20

	let cx, cy, px, py

	function CXY(e) { cx = e.clientX ; cy = e.clientY }

	window.onpointermove = function(e) { CXY(e) }
	window.onpointerdown = function(e) { CXY(e) }

	document.addEventListener( 'DOMContentLoaded', function() {

		const R = firebase.database().ref("A"+color);

		setInterval( function() {
		if (( cx , cy ) != ( px , py )) { 
		R.update( { 'x': cx, 'y': cy } )
		console.log( "updating firebase" , "mouseloc:" , cx , cy )
		px = cx ; py = cy
		}}, tickrate )

	})

</script>
